/* -------------------------------------------------------------------------------- */
/* Tablet */
@media screen and (min-width: 768px) {

  .input-block {
    width: 70%;
  }

  .flex-header {
    padding: 1% 1% 1.35% 1%;
  }

}


/* -------------------------------------------------------------------------------- */
/* Desktop */
@media screen and (min-width: 992px) {

  /* Hover elements are best suited to desktop */
  .submit-btn:hover {
    border: 1px solid white;
    color: white;
  }

  .context-link:hover {
    color: antiquewhite;
  }

  .input-block {
    width: 60%;
  }

  .flex-header {
    padding: 1.2% 1% 1.35% 1%;
  }


};

/* -------------------------------------------------------------------------------- */


/* React Defaults */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* -------------------------------------------------------------------------------- */